* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

.wrapper {
    max-width: 1920px;
    margin: 0 auto;
}

.top-media {
  position: fixed;
  z-index: 1;
  display: flex;
  width: 100%;
  max-width: 1920px;
  padding: 5px 0;
  background-color: white;
  box-shadow: 0 3px 3px #999;
  
  &__link{
    font-size: 35px;
    padding: 5px  30px 5px 0;
    color:royalblue;
  }
  &__link.fa-instagram{
    color:rgb(238, 7, 123);
  }
  @media (max-width:799px){
    font-size: 30px;
    padding: 3px 0;
  }
  &__slogan{
    display: block;
    align-self: center;
    padding-left: 25%;
    font-family: 'Dancing Script', cursive;
    font-size: 25px;
    flex-grow: 1;
    @media (max-width:799px){
      padding-left: 5%;
      font-size: 15px;
    }
  }
}

.menu{
  box-shadow: 0 3px 2px #999;
  padding: 20px 0;;
  &__list {
    width: 100%;
    display: flex;
    justify-content: space-evenly;
    list-style: none;
    @media(max-width: 799px){
    position: fixed;
    z-index: 2;
    top:0;
    left:100%;
    // top:25px;
    // left:20%;
    height: 100%;
    flex-direction: column;
    background-color:rgba($color: white, $alpha: 1);
    opacity: 0;
    padding-top: 70px;
    padding-bottom: 50px;
    transition: 0.3s;
  }
}
&__list--section{
  display: flex;
  flex-wrap: wrap;
  width: 100%;
  justify-content: space-evenly;
  align-self: center;
  @media(max-width: 799px){
    position: inherit;
    z-index: 1;
    top:0;
    left:0;
    // top:25px;
    // left:20%;
    height: 100%;
    flex-direction: row;
    background-color: none;
    opacity: 1;
    padding-top: 70px;
    padding-bottom: 50px;
    transition: 0.3s;
  }
}
&__element {
  display: flex;
  width: 200px;
  align-items: center;
  
  @media (max-width:799px) {
    width: 80%;
  }
  }
&__link {
  flex-basis: 100%;
  display: flex;
  height: 70px;
  align-items: center;
  justify-content: center;
  text-align: center;
  background-color: white;
  text-decoration: none;
  color: #333;
  border: 2px solid #444;
  text-transform: uppercase;
  letter-spacing: 1px;
  font-family: arial;
  transition: .2s;
  padding: 15px 20px;
  @media (max-width:799px){
    border: none;
    border-bottom: 1px solid #444;
    margin: 0 15px;
  }
  &:hover{
 /* font-weight: 900; */
 background-color: #444;
 color: white;
  }
}
&__link--section{
  font-size: 20px;
  font-weight: 900;
  background-size:100%;
  background-position: center;
  width: 100%;
  height: 100%;
  margin-bottom: 20px;
  color: white;
  transition: 0.5s;
  opacity: 1;
  &__btn{
    z-index: 3;
  opacity: 1;
  }
  &::before{
    opacity: 0.1;
    content: "";
    position: absolute;
    top:0;
    left:0;
    background-color: black;
    width: 100%;
    height: 100%;
  }
  &:hover{
    background-size: 105%;
    color: white;
    // transform: scale(1.1); other option to choose from
  }
  &:hover::before{
    opacity: 0.3;
    color: white;
    // transform: scale(1.1); other option to choose from
  }

  @media (max-width:799px){
    margin: 0;
  }
  
  }
  &__close {
    display: none;
    @media (max-width:799px) {
      display: block;
      position: absolute;
      top:7%;
      right:25%;
      font-size: 40px;
      cursor: pointer;
      color: #444;
      padding: 4px 7px;
      border-radius: 15%;
      background-color: rgba($color: #999, $alpha: .4);
    }
  
  }
  &__element-section{
    position: relative;
    background-color: black;
    margin-top: 30px;
    width:  27%;
    height: 180px;
    @media(max-width:799px){
      width: 45%;
    }
    @media(max-width:391px){
      width: 80%;
    }
    

  }
}

.menu--section{
  display: flex;
  height: 100%;
  flex-grow: 1;
  box-shadow: none;
}

.header{
  height: 400px;
  position: relative;
  &__logo{
    position: absolute;
    margin-top: 25px;
    top:50%;
    left: 50%;
    transform: translate(-50%, -50%);
    width: 350px;
  }
  &__menu {
    z-index: 2;
    display: none;
    @media(max-width:799px){
    display: block;
    position: fixed;
    background-color: rgba($color: #999, $alpha: .4);
    top:100px;
    padding: 4px;
    color: #444;
    border-radius: 20%;
    right:25px;
    font-size: 30px;
    cursor: pointer;
   
    }
    
  }
}

.sections {
  display: flex;
  width: 100%;
  min-height: 400px;
  flex-grow: 1;
  flex-direction: column;
  overflow: hidden;
  justify-content: space-between;
  margin-bottom: 20px;
}

.contener img {
  margin-top: 30px;
  display: flex;
  width: 100%;
}

.section-information{
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  min-width: 40%;
  text-align: center;
  @media (max-width:799px){
    width: 200px;
  }
}

//testing motion
div.motion-div{
  display: flex;
  width: 100%;

}

.footer {
  padding: 5px 0px 5px 30px;
  background-color: #333;
  color: white;
}

.active {
  background-color: #444;
  color:white;
}

.section-btn-text{
  text-shadow:1px 1px #333;
}